.button {
  font-family: inherit;
  font-size: 16px;
  line-height: 24px;
  color: $basic-white;
  background-color: $basic-blue;
  border: none;
  font-weight: bold;
  padding: 18px 32px;
  box-shadow: 0 22px 44px rgba($basic-blue, 0.18);
  border-radius: 8px;
  margin-left: auto;
  display: block;
}

.button_brif {
  background-color: transparent;
  border: 1.5px solid $basic-blue;
  padding: 10px 30px;
  border-radius: 5px;
  color: $basic-blue;
  box-shadow: none;
}
